body { 
       height: 100%; 
       margin: 0; 
       background: url("zelda.jpg") no-repeat center center fixed; 
       background-size: cover;
}
h4{
  font-family: "Arial Black", Gadget, sans-serif;
}

.content {
        min-height: 100%;
      }
      .content-inside {
        padding: 20px;
        padding-bottom: 50px;
      }
      .header {
        padding: 60px;
        text-align: center;
        background-color:rgb(0,0,0, .2);
        color: rgb(255,255,255);
        font-size: 30px;
        font-weight: bold;
        font-family: "Arial Black", Gadget, sans-serif;
        
      }
      .header a{
        display: block;
        width: 150px;
        color: rgb(255, 255, 255);
        text-decoration: none;
        font-weight: bold;
        font-family: "Arial Black", Gadget, sans-serif;
        font-size: 30px;
        -webkit-transition: opacity 1s ease-in-out;
        -moz-transition: opacity 1s ease-in-out;
        -ms-transition: opacity 1s ease-in-out;
        -o-transition: opacity 1s ease-in-out;
        transition: opacity 1s ease-in-out;
        opacity: 1;
      }
      .header li{
        display:inline-block;
        padding:6px;
        border-width:0px;
      }
      .header a:hover {
        opacity: 0.1;
        background-color: #ffffff85;   
    }
      .videoTag{
        height: 100%;
        width: 100%;
        float: left;
        top: 0;
        padding: none;
      }
      .footer{
        background: rgb(0,0,0);
        color: rgb(255, 255, 255);
        padding: 25px 50px;
        margin-top: -50px;
        text-align: center;
      }
      .footer h1{
        font-weight: bold;
        font-family: "Arial Black", Gadget, sans-serif;
        font-size: 30px;
        text-align: center;
      }
      .footer a{
        margin: 0 1rem;
        transition: transform 250ms;
        display: inline-block;
        color: rgb(255, 255, 255);
      }
      .footer a:hover{
        transform: translateY(-2px);
      }
      

      

