.content{
    margin: 40px;
}
.content-inside{
    display: grid;
    grid-gap: 10px;
    grid-template-columns: auto auto auto;
    color: #444;

}
.box {
    background-color:rgb(0,0,0, .2);
    color: rgb(255,255,255);
    border-radius: 5px;
    padding: 20px;
    font-size: 150%;

}

.projects {
    grid-column: 1 / span 2;
}
.skills {
    grid-column: 3 ;
    grid-row: 1 / span 2;
}
.work {
    grid-column: 1 ;
    grid-row: 2 ;
}
.programming {
    padding: 80px;
    padding-bottom: 50px;
    grid-column: 2 ;
    grid-row: 2 ;
}

ul{
    list-style-type: none;
    padding: 0;
    text-align: center;
}

li{
    padding: 5px 10px;
    margin: 0 0 3px 0;
    color:rgb(255, 255, 255);
    font-family: "Arial Black", Gadget, sans-serif;
}

h3{
    text-align: center;
    font-family: "Arial Black", Gadget, sans-serif;
    font-weight: 900px;
    color: rgb(255, 255, 255);
}

.buttonDaddy{
    background: rgb(219, 216, 216);
    color: black;
    padding: 16px 25px;
    margin-top: 10px;
    margin-bottom: 10px;
    margin-right: 50px;
    margin-left: 100px;
    border: none;
    cursor: pointer;
    width: 30%;
    border-radius: 8px;
    display: block; 
    font-family: "Arial Black", Gadget, sans-serif;
}
